import React, { useRef } from 'react'
import { Button, Modal } from 'antd'
import ReactToPrint from 'react-to-print';
import PrintProductDate from '../comp/printProductDate';
import { theme } from '../../../static/theme';
import { CSVLink } from "react-csv";
import { reportProduct } from '../../../asset/column/report';
import { addIndex } from '../../../functions/fn';

export default function PrintProductByDate({ open, setOpen, data, date }) {
    const componentRef = useRef()

    const csvReport = {
        data: addIndex(data),
        headers: reportProduct,
        filename: '_.xls'
    };

    return (
        <Modal
            title=""
            visible={open}
            width={1000}
            onCancel={() => setOpen(!open)}
            footer={[
                <CSVLink
                    className='go-toCSV'
                    {...csvReport}
                    key="excel"
                    style={{
                        float: "left"
                    }}
                >
                    Export to CSV
                </CSVLink>,
                <Button
                    danger
                    key="cancel"
                    size={theme.btnSize}
                    onClick={() => setOpen(!open)}
                >
                    
                </Button>,
                <ReactToPrint
                    key="post"
                    trigger={() => {
                        // NOTE: could just as easily return <SomeComponent />. Do NOT pass an `onClick` prop
                        // to the root node of the returned component as it will be overwritten.
                        return <Button
                            type="default"
                            size={theme.btnSize}
                        >
                            
                        </Button>;
                    }}
                    content={() => componentRef.current}
                />
            ]}
        >
            <PrintProductDate ref={componentRef} data={data} date={date} />
        </Modal>
    )
}
