import { useMutation } from '@apollo/client'
import { Col, Drawer, Input, Row, Form, Button } from 'antd'
import React, { useEffect, useState } from 'react'
import { convertTel, convertUpdateData, mutationCallBackFn, noticeAction } from '../../../functions/fn'
import { ADD_CUSTOMER, GET_ALL_CUSTOMER, UPDATE_CUSTOMER } from '../../../graphql/customer'
import { theme } from '../../../static/theme'

export default function AddUpdateCustomer({ open, setOpen, data, type }) {
  let [form] = Form.useForm()

  const [setCustomers] = useMutation(ADD_CUSTOMER, mutationCallBackFn(GET_ALL_CUSTOMER, 'getCustomers'))

  const [updateCustomers] = useMutation(UPDATE_CUSTOMER, mutationCallBackFn(GET_ALL_CUSTOMER, 'getCustomers'))

  const [updateData, setUpdateData] = useState({})

  useEffect(() => {
    setUpdateData(data)
  }, [data])

  const onFinish = (values) => {
    let array = {
      ...values,
      tel: convertTel(values.tel)
    }

    if (type === "add") {
      setCustomers({
        variables: {
          input: array
        },
        update(_, result) {
          // console.log(result)
          noticeAction("success", "")

          setOpen(false)
          form.resetFields()
        },
      })
    } else {
      updateCustomers({
        variables: {
          input: {
            ...array,
            id: data.id
          }
        },
        update(_, result) {
          // console.log(result)  
          noticeAction("success", "")

          setOpen(false)
          form.resetFields()
        },
      })
    }

  };

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <Drawer
      title=""
      width={500}
      onClose={() => setOpen(false)}
      visible={open}
      bodyStyle={{ paddingBottom: 80 }}
    >
      <Form
        form={form}
        layout="vertical"
        hideRequiredMark
        fields={convertUpdateData(type, updateData)}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Row gutter={16}>
          <Col xs={24} md={12}>
            <Form.Item
              name="lname"
              label=""
              rules={[{ required: true, message: '!' }]}
            >
              <Input
                placeholder="Lastname"
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={12}>
            <Form.Item
              name="fname"
              label=""
              rules={[{ required: true, message: '!' }]}
            >
              <Input
                placeholder="Firstname"
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={12}>
            <Form.Item
              name="tel"
              label=""
              // rules={[{ required: true, message: '!' }]}
            >
              <Input
                prefix="+855(0)"
                placeholder="Telephone"
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={12}>
            <Form.Item
              name="email"
              label=""
              rules={[{  message: '!', type: "email" }]}
            >
              <Input
                placeholder="Email"
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={24}>
            <Form.Item
              name="address"
              label=""
              // rules={[{ required: true, message: '!' }]}
            >
              <Input
                placeholder="Address"
              />
            </Form.Item>
          </Col>
          <Col xs={24} md={24}>
            <Form.Item>
              <Button
                htmlType="submit"
                type="primary"
                style={{
                  width: "100%"
                }}
                size={theme.btnSize}
              >
                
              </Button>
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Drawer>
  )
}
