import { Descriptions, Divider, Modal, Form, Row, Col, InputNumber, Button, Input } from 'antd'
import React, { useEffect, useState } from 'react'
import { convertEditData } from '../../../functions/fn'
import { theme } from '../../../static/theme'

export default function UpdateCart({ open, setOpen, selectData, cartData, setCartData }) {

    const [data, setData] = useState({})

    useEffect(() => {
        setData(selectData)
    }, [selectData])

    const onFinish = (values) => {
        let newArray = [...cartData]

        let index = cartData?.findIndex(ele => ele.product === selectData.id)

        newArray[index].qty = values.qty
        newArray[index].price = values.price
        newArray[index].total = values.qty * values.price
        newArray[index].remark = values.remark

        setCartData(newArray)
        setOpen(false)
    }

    const onFinishFailed = (err) => {
        console.log(err)
    }

    return (
        <Modal
            title={""}
            visible={open}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <Descriptions bordered>
                <Descriptions.Item label="" span={3}>{data?.description}</Descriptions.Item>
                <Descriptions.Item label="" span={3}>{data?.id}</Descriptions.Item>
                <Descriptions.Item label="" span={3}>{data?.price}$ /{data?.um}</Descriptions.Item>
            </Descriptions>
            <Divider></Divider>
            <Form
                name="addCart"
                fields={convertEditData(data)}
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row gutter={16}>
                    <Col
                        xs={10}
                    >
                        <Form.Item
                            name="qty"
                            rules={[
                                {
                                    required: true,
                                    message: '!',
                                    type: "number"
                                }
                            ]}
                        >
                            <InputNumber
                                style={{
                                    width: "100%"
                                }}
                                placeholder=""
                                size={theme.inputSize}
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={4}
                    >
                        <center>
                            X
                        </center>
                    </Col>
                    <Col
                        xs={10}
                    >
                        <Form.Item
                            name="price"
                            rules={[
                                {
                                    required: true,
                                    message: '!',
                                    type: "number"
                                }
                            ]}
                        >
                            <InputNumber
                                formatter={value => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                parser={value => value.replace(/\$\s?|(,*)/g, '')}
                                style={{
                                    width: "100%"
                                }}
                                size={theme.inputSize}
                                placeholder=""
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <Form.Item
                            name="remark"
                        >
                            <Input
                                placeholder=""
                            />
                        </Form.Item>
                    </Col>
                    <Col
                        xs={24}
                    >
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{
                                width: "100%"
                            }}
                            size={theme.btnSize}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
